body {
  font-family: "Segoe UI", "Roboto", sans-serif;
  background: #f4f4f4;
  margin: 0;
  padding: 20px;
}

h2 {
  text-align: center;
}

#music {
  border: 1px solid #ccc;
  background: #fff;
  padding: 10px;
  margin: 20px auto;
  width: 90%;
}

#controls {
  text-align: center;
}

button {
  margin: 5px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background: #3f51b5;
  color: white;
  cursor: pointer;
}

button:hover {
  background: #2c3a8c;
}

#status {
  text-align: center;
  margin-top: 10px;
}
